"use client"
import React from 'react'
import Link from 'next/link'
import { useQuery } from '@tanstack/react-query'
import API from '@/lib/api'

async function fetchGroups() {
  const res = await API.get('/groups')
  return res.data.groups
}

export default function GroupsPage() {
  const { data: groups = [], isLoading } = useQuery({ queryKey: ['groups'], queryFn: fetchGroups })

  return (
    <div className="p-6">
      <h2 className="mb-4 text-2xl font-semibold">Groups</h2>
      {isLoading && <p>Loading...</p>}
      <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
        {groups.map((g: any) => (
          <Link key={g.id} href={`/groups/${g.id}`} className="rounded bg-white/20 p-4">
            <h3 className="text-lg font-semibold text-pink-500">{g.name}</h3>
            <p className="text-sm text-zinc-300">{g.city}</p>
          </Link>
        ))}
      </div>
    </div>
  )
}
